body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
  background-color: black;
  padding: 2em;
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.link {
  color: white;
  text-decoration: none;
  padding: .5em;
  font-size: 1.2em;
}

.itemLink {
  text-decoration: none;
  color: black;
}

.white-text {
  color: white;
  font-size: 1.2em;
}

.wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-align-content: center;
          align-content: center;
  height: 100%;
}

select {
  margin: 1.5em;
}

.no-items {
  padding: 2em;
  text-align:center;
  font-size: 2em;
  width: 100%;
}

.confirm-button {
  position: absolute;
  bottom: 2em;
  right: 2em;
}
img {
    height: 19vh;
    margin: 0 auto;
}

.product {
    width: 20em;
    padding: 1.2em;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    /* margin:.2em; */
}

.title {
    text-align: center;
    font-size: 1.5em
}

.price {
    margin-top: 1.5em;
}

.title, .description {
    margin: 1.5em 0;
}
.container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}
.detail-product {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    width: 50em;
    margin: 0 auto;
}

.detail-img {
    height: 30vh;
}

.addCart {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

input {
    padding: .5em;
    font-size: 1em;
    outline: none;
    border-radius: 5px;
    border: 1px solid #adadad;
}

button {
    color: white;
    background-color: black;
    border: 1px solid transparent;
    padding: .5em;
    margin: .5em;
    border-radius: 5px;
    font-size: 1em;
    outline: none;
}

.detail-price {
    text-align: center;
    margin-bottom: 2em;
}
